.App {
  text-align: center;
}

.container {
	margin: auto;
	max-width: 900px;
	width: 90%;
}
img {
	height: 100%;
	max-width: 100%;
	object-fit: cover;
}

.projects {
	display: flex;
	flex-flow: row wrap;
	justify-content: space-between;
	width: 100%;
}
.project {
	border: 1px solid #000;
	border-radius: 4px; 
	-webkit-box-shadow: 0px 0px 0px 0px rgba(0,0,0,0);
	-moz-box-shadow: 0px 0px 0px 0px rgba(0,0,0,0);
	box-shadow: 0px 0px 0px 0px rgba(0,0,0,0);
	cursor: pointer;
	display: flex;
	flex-flow: column;
	margin: 1em 0;
	overflow: hidden;
	position: relative;
	height: 238px;
	width: 48%;
	transition: .3s;
}
.project:hover {
	-webkit-box-shadow: 0px 0px 7px 2px rgba(0,0,0,0.35);
	-moz-box-shadow: 0px 0px 7px 2px rgba(0,0,0,0.35);
	box-shadow: 0px 0px 7px 2px rgba(0,0,0,0.35);
	transform: scale(1.1);
}
.project:hover .overlay {
	transform: translate(0%, 0);	
}
.project .overlay {
	background: dodgerblue;
	box-sizing: border-box;
	color: #fff;
	padding: 1em;
	position: absolute;
	top: 0;
	left: 0;
	overflow-y: auto;
	height: 100%;
	width: 100%;
	transform: translate(-100%, 0);
	transition: .3s;
	z-index: 1;
}

@media (max-width: 680px) {
	.project {
		width: 100%;
	}
}